<template>
     <div id="create">
        <div class="create_header">
            <div class="back" @click="handleBack">
            <i class="iconfont icon-huitui" />
            <p class="header_info"></p>
            </div>
            <div class="header_title"></div>
        </div>
        <div class="createFrom">
            <mt-field label="" placeholder="" :state="(formData.product_name?'':error)" type="text" v-model="formData.product_name" ></mt-field>
            <mt-field :label="''" placeholder="0.00" :state="formData.price?'':error" type="number" v-model="formData.price" @change="changemoney(formData)"></mt-field>           
            <a data-v-4a389152="" class="mint-cell mint-field"> 
                <div class="mint-cell-left"></div> 
                <div class="mint-cell-wrapper" style="background-origin: content-box;">
                    <div class="mint-cell-title"> 
                        <span class="mint-cell-text" style="marginRight:.05rem"></span> 
                    </div> 
                    <div class="mint-cell-value">
                        <el-input-number v-model="formData.num" @change="changemoney(formData)" :min="1" :max="99" label="" precision="0" size="mini" :disabled="formData.num_flag"></el-input-number>
                        <div class="mint-field-clear" style="display: none;"><i class="mintui mintui-field-error"></i></div> 
                         <div class="mint-field-other"></div>
                    </div> 
                </div> 
                <div class="mint-cell-right">
                </div>
            </a>
            
            <!-- <mt-field  label="" :disabled="formData.num_flag" :state="formData.num?'':num_error" placeholder="" type="number" v-model="formData.num" @input="changemoney(formData)"></mt-field> -->
            <mt-field :label="''" :disabled="formData.num_flag" placeholder="0.0" :state="formData.discount?'':num_error" type="number" v-model="formData.discount" @input="changemoney(formData)"></mt-field>           
            <mt-field :label="''" placeholder="0.00"  type="text" v-model="formData.total" @input="changemoney(formData)" disabled></mt-field>           
            <mt-field :label="''" placeholder=""  type="number" v-model="formData.fare" ></mt-field>           

            <mt-checklist
            align="right"
            v-model="formData.num_flag"
            :options="option"
            @change="saveData(formData)">
            </mt-checklist>
            <mt-field label="" placeholder="" :state="formData.introduction?'':error" type="textarea" rows="2" v-model="formData.introduction" ></mt-field> 
            <el-dialog
            title=""
            :visible.sync="dialogVisible"
            width="80%"
            :close-on-click-modal="false"
            :before-close="handleClose">
            <el-dialog
            width="80%"
            title=""
            :visible.sync="innerVisible"
            append-to-body>
                <el-input placeholder="" v-model="addtext" class="input-with-select">
                    <el-button slot="append" icon="el-icon-plus" @click="handleAdd"></el-button>
                </el-input>
            </el-dialog>
                <el-select
                    v-model="category_id"
                    filterable
                    remote
                    reserve-keyword
                    placeholder=""
                    :remote-method="getlist"
                    >
                    <el-option
                    v-for="item in categorylist"
                    :key="item.category_id"
                    :label="item.category_name"
                    :value="item.category_id">
                    </el-option>
                    <el-option value="">
                        
                    </el-option>
                </el-select>
                
                
                <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false"> </el-button>
                    <el-button type="primary" @click="submit"> </el-button>
                </span>
            </el-dialog>
                <!--  -->
                <div v-if="isShow" class="weui-gallery" style="display:block">
                    <span class="weui-gallery__img" :style="'background-image: url('+src+');'" @click="()=>{this.isShow=false}"></span>
                    <div class="weui-gallery__opr" @click="handleDel">
                        <a href="javascript:" class="weui-gallery__del">
                            <i class="weui-icon-delete weui-icon_gallery-delete"></i>
                        </a>
                    </div>
                </div>
                <!--  -->
               <div class="weui-cell  weui-cell_uploader" style="background:#fff">
                <div class="weui-cell__bd">
                    <div class="weui-uploader">
                        <div class="weui-uploader__hd">
                            <p class="weui-uploader__title"><i class="iconfont icon-weibiaoti2" @click="handleTips" style="fontSize:.25rem;position:relative;bottom:-0.06rem" /></p>
                            <div class="weui-uploader__info">{{formData.image.length}}/9</div>
                        </div>
                        <div class="weui-uploader__bd">
                            <ul class="weui-uploader__files" id="uploaderFiles" v-if="formData.image.length">
                                <li  v-for="(item,index) in images" :key="index" class="weui-uploader__file"  @click="handleShow(item,index)">
                                    <img :src="item" style="width: 100%; height: 100%; object-fit: cover; padding: 5%; box-sizing: border-box;">
                                </li>
                            </ul>
                            <div class="weui-uploader__input-box" v-if="showAdd">
                                <div @click="chooseImage(upload)" style="width:100%;height:100%"></div>
                                <!-- <input   id="uploaderInput" class="weui-uploader__input" type="file" accept="image/*" multiple @change="chooseImage"> -->
                                <!-- <h5-cropper :option="option" @getbase64Data="getbase64Data" @getblobData="getblobData" @getFile="getFile" @change="uploadPhoto"></h5-cropper> -->
                            </div>
                        </div>
                    </div>
                </div>
            <!-- </div> -->
            </div>
            <div class="weui-form__control-area">
      <div class="weui-cells__group weui-cells__group_form">
        <div class="weui-cells weui-cells_checkbox" style="marginTop:0">
            <label class="weui-cell weui-cell_active weui-check__label" for="s11">
                <div class="weui-cell__hd">
                    <input type="checkbox" class="weui-check" name="checkbox1" id="s11" v-model="formData.address_flag">
                    <i class="weui-icon-checked"></i>
                </div>
                <div class="weui-cell__bd">
                    <p><span v-if="formData.address_flag" >()</span></p>
                </div>
            </label>
            <!-- <label class="weui-cell weui-cell_active weui-check__label" for="s12">
                <div class="weui-cell__hd">
                    <input type="checkbox" name="checkbox1" class="weui-check" id="s12" v-model="formData.num_flag"> 
                    <i class="weui-icon-checked"></i>
                </div>
                <div class="weui-cell__bd">
                    <p><span v-if="formData.num_flag">()</span></p>
                </div>
            </label> -->
        </div>
      </div>
    </div>            
        </div>
        <!-- <H5Cropper></H5Cropper> -->
        <div class="wallet_footer">
            <div class="footer_left footer_info" @click="handlePhoto"></div>
            <div class="footer_right footer_info" @click="handleSave"></div>
        </div>
     </div>
</template>
<script>
import { addProduct,updateProduct,getProductCategoryInfo,addCategoryList,getConfig} from '@/api'
import { fileURLToPath } from 'url'
import categoryVue from './category.vue'
import { Indicator,MessageBox } from 'mint-ui'
import { initWxconfig,getImage } from '@/utils/initWxConfig.js'
// import H5Cropper from '@/components/cropper'
import H5Cropper from "vue-cropper-h5";

export default {
    components:{
        H5Cropper
    },
    data(){
        return{
            value:false,
            addtext:'',
            isShow:false,
            showAdd:true,
            dialogVisible:false,
            innerVisible:false,
            change:'0',
            error:'',
            num_error:'',
            limit:0,
            index:-1,
            src:'',
            address_flag:'',
            num_flag:'',
            option:['()'],
            images:[],
            category_id:'',
            categorylist:[],
            formData:{
                product_name:'',
                price:'',
                num:1,
                address_flag:false,
                introduction:'',
                image:[],
                num_flag:false, 
                total:0,
                discount:10,
                fare:0
            }
        }
    },
    watch:{
        category_id(val,oldval){
            if(val===''){
                this.innerVisible=true
            }
        }
    },
    beforeMount () {
    this.$store.state.showTab = false
        if(this.$route.params.val===''){
            this.formData=this.$store.state.formData
        }
    },
  mounted(){
      let url = location.href.split('#')[0]
      console.log('url---:',url);
      getConfig({url:url}).then(res=>{
        let wxConfig = res.data.data.config
        initWxconfig(wxConfig)
      })
      let button = document.querySelector('.mint-checkbox-core')
      let btnwrap = document.querySelector('.mint-checkbox')
      let list =  document.querySelector('.mint-checklist')
      let title = document.querySelector('.mint-checklist-title')
      title.parentNode.removeChild(title)
      let wrap =document.querySelectorAll('.mint-cell-wrapper')
      wrap[2].style.backgroundOrigin='content-box'
       let a =document.querySelectorAll('.mint-cell-title')
       console.log(a)
       for(let i=0;i<a.length;i++){
        let des= document.createElement("i")
        des.className="iconfont icon-weibiaoti2"
        des.style.fontSize='.25rem'
        des.style.position="absolute"
        des.style.bottom='-0.08rem'
           if(i!==6){
        des.onclick=function showtip(){
            switch(i){
                case 0:
                    MessageBox.alert('()', '')
                    break
                case 1:
                    MessageBox.alert('()', '')
                    break
                case 2:
                    MessageBox.alert('(),', '')
                    break
                case 3:
                    MessageBox.alert('(),x.x,:8.8', '')
                    break
                case 4:
                    MessageBox.alert('(*)', '')
                    break
                case 5:
                    MessageBox.alert('(0)', '')
                    break
                case 7:
                    MessageBox.alert('()', '')
                    break
            }
        }
        let para = document.createElement("span");
        let node = document.createElement("span");
        let nodetext = document.createTextNode("*");
        node.appendChild(nodetext)
        para.appendChild(node);
        para.appendChild(des)
        para.children[0].style.color='red'
        para.style.position="relative"
        if(i==1||i==4||i==5){
           let key = document.createElement("span");
            let node = document.createTextNode("");
            key.appendChild(node);
            key.style.float='right'
            key.style.lineHeight='.25rem'
            a[i].appendChild(key)
        }
        a[i].appendChild(para)
       }else{
        a[i].children[0].style.padding=0
        a[i].children[0].children[1].style.margin=0
       }
       }
    
      getProductCategoryInfo().then(res=>{
          this.categorylist=res.data.data
          this.category_id=this.categorylist[0].category_id
      })
      if(this.$route.params.item){
          Indicator.close()
          this.formData=this.$route.params.item
          this.category_id=this.formData.category_id
          this.images=[...this.$route.params.image]
    if(this.$route.params.val===''){
            switch(this.$route.params.item.address_flag){
            case '0':
                 this.formData.address_flag=false
                break
            case '1':
                 this.formData.address_flag=true
                break
        }
        switch(this.$route.params.item.num_flag){
            case '0':
                 this.formData.num_flag=false
                break
            case '1':
                 this.formData.num_flag=true
                break
        }
                    }else{
                        switch(this.formData.address_flag){
            case '0':
                 this.formData.address_flag=false
                break
            case '1':
                 this.formData.address_flag=true
                break
        }
        switch(this.formData.num_flag){
            case '0':
                 this.formData.num_flag=false
                break
            case '1':
                 this.formData.num_flag=true
                break
        }
                    
          }
      }
  },
  methods:{
      dataURLtoFile(dataurl) {
            var filename='weixin'
            var arr = dataurl.split(','),
            mime = arr[0].match(/:(.*?);/)[1],
            bstr = atob(arr[1]),
            n = bstr.length,
            u8arr = new Uint8Array(n);
            while (n--) {
            u8arr[n] = bstr.charCodeAt(n);
            }
            return new File([u8arr], filename, { type: mime });
        },
      upload(res){
          var localData = res.localData;
             if (localData.indexOf('data:image') != 0) {                       
                     //                                               
                     localData = 'data:image/jpeg;base64,' +  localData                    
             }                    
             localData = localData.replace(/\r|\n/g, '').replace('data:image/jgp', 'data:image/jpeg');
        let camera = this.dataURLtoFile(localData)
           let URLClass = window.URL || window.webkitURL || window.mozURL
                    this.images.push(URLClass.createObjectURL(camera))
                    this.formData.image.push(camera)
                    if(this.formData.image.length>8){
                        this.showAdd=false
                    }
        // console.log(res.localData)// localDatabase64img
      },
    chooseImage(cb){
        let that=this
       wx.chooseImage({
        count: 9, // 9
        sizeType: ['original', 'compressed'], // 
        sourceType: ['album', 'camera'], // 
        success : function (res){
            if(res.localIds.length+that.images.length<=9){
                let i
                for(i=0;i<res.localIds.length;i++){
                    wx.getLocalImgData({
                        localId: res.localIds[i], // localID
                        success: cb
                    })
                }
            }else{
                that.$toast('9')
            }
        },
        })
    },
    handleTips(){
        MessageBox.alert('', '')
    },
      getlist(){
          getProductCategoryInfo().then(res=>{
              this.categorylist=res.data.data
          })
      },
      handleAdd(){
          if(this.addtext!=''){
            if(this.categorylist.length+1>5){
                this.$toast('')
                this.category_id=''
            }else{
            addCategoryList({category_name:this.addtext}).then(res=>{
              if(res.data.success===1){
                  this.$toast(','+(5-this.categorylist.length)+'')
                    this.addtext=''
                    this.getlist()
                    this.innerVisible=false
              }
            })
            }
          }else{
              this.$toast('')
          }
      },
      saveData(item){
      let button = document.querySelector('.mint-checkbox-core')
            if(item.num_flag===false){
                this.num_error=''
                this.formData.num=1
                this.formData.total=this.formData.price*this.formData.num
            }else{
                this.formData.num=1
            }
        //   this.$store.state.formData=this.formData
      },
      changemoney(item){
          console.log(item.price)
          if(item.price===''){
              item.price=0
          }
              this.formData.price=parseFloat(this.formData.price).toFixed(2)
              this.formData.total=parseFloat(this.formData.price*this.formData.num).toFixed(2)
      },
      //
      handleSave(){
        //   this.getlist()
          if(this.formData.product_name!=''&&this.formData.price!=''&&this.formData.introduction!=''){
              this.dialogVisible=true
          }else{
              this.error='error'
              if(this.formData.num_flag===false){
                  this.num_error='error'
              }
              this.$toast('')
        }
      },
      //dialog
      handleClose(){
            this.dialogVisible = false
      },
      //
      handleBack(){
          this.$router.push('/goods')
          this.$store.state.formData={
                product_name:'',
                price:'',
                num:1,
                address_flag:false,
                introduction:'',
                image:[],
                num_flag:false, 
                total:0,
                discount:10,
                fare:0
            }
      },
      //
      handleShow(src,index){
          this.isShow=true
          this.src=src
          this.index=index
      },
      //
      handleDel(){
          this.formData.image.splice(this.index,1)
          this.images.splice(this.index,1)
          if(this.formData.image.length<9){
              this.showAdd=true
          }
          this.isShow=false
      },
      //
      handlePhoto(){
          if(this.formData.product_name!=''&&this.formData.price!=''&&this.formData.introduction!=''){
              this.$router.push({name:'createqr',params:{
                item:this.formData,
                image:this.images,
                val:this.$route.params.val
      }})
          }else{
              this.error='error'
              if(this.formData.num_flag===false){
                  this.num_error='error'
              }
              this.$toast('')
        }
      
    },
    //
      submit(){
        //   this.handleAdd()
          if(this.$route.params.val===''){
          switch(this.$route.params.item.address_flag){
            case true:
                 this.address_flag='1'
                break
            case false:
                 this.address_flag='0'
                break
        }
        switch(this.$route.params.item.num_flag){
            case true:
                 this.num_flag='1'
                break
            case false:
                 this.num_flag='0'
                break
        }
          }else{
              switch(this.formData.address_flag){
            case true:
                 this.address_flag='1'
                break
            case false:
                 this.address_flag='0'
                break
        }
        switch(this.formData.num_flag){
            case true:
                 this.num_flag='1'
                break
            case false:
                 this.num_flag='0'
                break
        }
          }
        if(this.$route.params.val===''){
             let data=new FormData()
             for(let i=0;i<this.formData.image.length;i++){
            data.append("img"+i,this.formData.image[i])
                 }     
            data.append("product_name",this.formData.product_name)
            data.append("value",this.formData.value)
            data.append("price",this.formData.price)
            data.append("introduction",this.formData.introduction)
            data.append("category_id",this.category_id)
            data.append('address_flag',this.address_flag)
            data.append('num_flag',this.num_flag)
            data.append('num',this.formData.num)
            data.append('total',this.formData.total)
            data.append('discount',this.formData.discount)
            data.append('fare',this.formData.fare)
            addProduct(data).then(res=>{
                if(res.data.success===1){
                    this.$router.push('/goods')
                    this.formData={
                    product_name:'',
                    value:[],
                    price:'',
                    detail:'',
                    image:[],
                    }
                }
          })
        }else{
             let data=new FormData()
             for(let i=0;i<this.formData.image.length;i++){
            data.append("img"+i,this.formData.image[i])
                 }     
            data.append("product_name",this.formData.product_name)
            data.append("value",this.formData.value)
            data.append("price",this.formData.price)
            data.append("introduction",this.formData.introduction)
            data.append("category_id",this.category_id)
            data.append("product_id",this.formData.product_id)
            data.append('address_flag',this.address_flag)
            data.append('num_flag',this.num_flag)
            data.append('num',this.formData.num)
            data.append('total',this.formData.total)
            data.append('discount',this.formData.discount)
            data.append('fare',this.formData.fare)

            updateProduct(data).then(res=>{
                 if(res.data.success===1){
                    this.$router.push('/goods')
                this.formData={
                  product_name:'',
                  value:[],
                  price:'',
                  detail:'',
                  image:[],
              }
                 }
            })
        }
          
      },
      //
      uploadPhoto(e){
           let URLClass = window.URL || window.webkitURL || window.mozURL
           let  src,files = e.target.files
           this.limit+=e.target.files.length
           if(e.target.files.length===1){

           }
           let sum = this.formData.image.length+e.target.files.length
           if(sum>9){
               console.log('')
           }else{
        for (let i = 0 ; i < files.length; ++i) {
            let file = files[i]
            console.log(files[i])
            if (URLClass) {
            src = URLClass.createObjectURL(file)
            } else {
            src = e.target.result
            }
            // console.debug( "showImg:src",src)
            let photo = {
            originalUrl: src
            }
            file.src = src
            this.formData.image.push(file)
            // this.images.push(photo.originalUrl)
            this.saveData(this.formData)
      }
            if(this.formData.image.length>8){
                        this.showAdd=false  
                    }
                }
            }
  }
}
</script>
<style lang="scss" scoped>
.mint-field-core{
    resize:none!important;
}
#create{
height: 100%;
overflow: auto;
 .create_header{
            width:100%;
            height: .45rem;
            line-height: .45rem;
            // background:#fff;
            color:black;
            position: fixed;
            top: 0;
            left: 0;
            text-align: center;
            display:flex;
            .back{
                float: left;
                .icon-huitui{
                    float: left;
                    font-size: .16rem;
                }
            }
            .header_info{
                font-size: .14rem;
                padding-right: .1rem;
                padding-left: .2rem;
                text-align: left;
            }
            .header_title{
                flex: 1;
                padding-right: .58rem;
            }
        }

        .createFrom{
            margin: .45rem 0;
            .mint-checklist-label{
                padding:0;
            }
            .weui-cell{
                background: #fff;
            }
            .dialog{
                height: 1rem;
                padding:.27rem 0;
                box-sizing: border-box;
                background: black;
                position:fixed;
                left: .4rem;
                top:3rem;
                z-index: 999;
                width: 80%;
            }
        }
        .weui-form__control-area{
            margin:0;
        }
}
.wallet_footer{
        position: fixed;
        bottom: 0;
        left: 0;
        z-index:999;
        background: #fff;
        width:100%;
        height: .45rem;
        line-height: .45rem;
        text-align: center;
        display:flex;
        .footer_info{
            flex:1;
        }
        .footer_left{
            background: #04BE02;
            border-right: 1px solid #c0c0c0;
            color: #fff;
        }
        .footer_right{
            background: #fff;
            color:#04BE02;
        }
    }
</style>